// have to loop throught interval
for(let i =0; i < 10; i++){
    let timeStart = Date.now();
    (function(j){
        let timeNow = Date.now();
        while((timeNow - timeStart) <= 1000){
            console.log(timeNow - timeStart);
            timeNow = Date.now();
            setTimeout(()=>{
                timeEnd = Date.now();
                console.log(timeEnd);
            }, 1000)
        }
        console.log(j)
    })(i)
}
